.intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
}

.bg{
    position:absolute;
    top: 4rem;
    right: 4rem;
    z-index: -1;
    object-fit: cover;
    height: 100vh;

}

.introContent{
    height: 100vh;
    width: 100vh;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hello{
    font-size: 1.75rem;
    font-weight: 100;
}
.introName{
    color: rgb(209, 101, 23);

}

.introPara{
    font-size: medium;
    font-weight: 200;
    letter-spacing: 1px;
}

.btn{
    background: rgb(218, 192, 192);
    margin: 1rem 0;
    padding: 1rem 1.5rem;
    border: none;
    border-radius: 2rem;
}

.btnImg{
    object-fit: cover;
    margin: 0 0.30rem;
    height: 1rem;
}

@media screen and (max-width:500px){
    .bg{
        right: -35vw;
    }
    .introContent{
        font-size: 10vw;
    }
}